
@charset "UTF-8";
/*顶部nav*/
.youzhong {
  float: left;
  margin-top: 25px;
  margin-right: 20px;
  width: 100%;
}
.youzhong a img {
  display: inline-block;
}
.s_form {
  float: right;
  width: 490px;
  height: 55px;
}
.s_form input {
  width: 325px;
  height: 30px;
  border: solid 1px #CCCCCC;
  float: left;
  border-radius: 2px;
}
.text {
  position: relative;
}
.go_car {
  width: 20px;
  height: 20px;
  background: url(~@/assets/images/ss.png);
  position: absolute;
  left: 295px;
  top: 5px;
  cursor: pointer;
}
#go_cars {
  width: 20px;
  height: 20px;
  border: none;
}
.s_form {
  padding-left: 5px;
}
.s_form .buttons {
  padding: 0 9px;
  background: #BB1E7B;
  border-radius: 2px;
  font-size: 15px;
  float: left;
  color: #FFFFFF;
  border: none;
  cursor: pointer;
}
.s_form .buttons i {
  width: 20px;
  height: 20px;
  background: url(~@/assets/images/gouwu.png);
  display: block;
  float: left;
  margin-right: 7px;
  margin-top: 6px;
}
.s_form button span {
  line-height: 32px;
}
.s_form p {
  clear: both;
  line-height: 26px;
}
.s_form p a {
  padding-left: 10px;
  font-size: 12px;
}
.s_form p .p_list {
  color: #D674AD;
}
body #mainhead .content .btn_area__hotkeyword .btn_area__hotkeyword__item--hot {
  color: #CB569A;
}
.bar_content li a {
  color: #ffffff;
}



